// html
<template>
  <!-- v-if="조건식"
  동적 ui 만드려면 현재 상태의 ui를 데이터로 저장
  데이터에 따라 ui가 어떻게 보일지 작성 -->
  <div class="black-bg" v-if="modal == true">
    <div class="white-bg">
      <h4>상세페이지</h4>
      <p>상세페이지</p>
      <button @click="modal = false">닫기</button>
    </div>
  </div>
  <div class="menu">
    <!-- v-for="변수 in 몇번"
    v-for="변수 in 데이터"
    :key="" 반복문 돌린 요소를 구분하기위해
    v-for="(a,i) in 데이터" i 증가 -->
    <a v-for="a in 3" :key="a">home</a>
    <a v-for="b in menus" :key="b">{{ b }}</a>
    <a v-for="(c,i) in menus" :key="c">{{ i }}</a>
  </div>
    <!-- 데이터 바인딩
    밑의 data()의 데이터를 쓸수있음
    자동 렌더링
    자주 변동되는 데이터
    html 속성도 가능 ': 속성' -->
  <!-- <div>
    <h4 class="blue" :style="스타일">{{ products[0]}}</h4>
    <p>{{ price1 }} 만원</p>
  </div>
  <div>
    <h4>{{ products[1]}}</h4>
    <p>{{ price2 }} 만원</p>
  </div>
  <div>
    <h4>{{ products[2]}}</h4>
    <p>{{ price2 }} 만원</p>
  </div> -->
  <!--이벤트 v-on:click="", @click=""
  click, mouseover 등등 사용 가능 -->
  <div v-for="(a,i) in products" :key="a">
    <!-- ./ 현재경로 -->
    <img :src="a.image" alt="" class="room">
    <h4 @click="modal = true">{{ a.title}}</h4>
    <p>{{a.content}}</p>
    <p>{{a.price}}</p>
    <button v-on:click="plus(i)">신고</button>
    <span>신고수 : {{신고수[i]}}</span>
  </div>

</template>

<script>

import products from './assets/room.js';
// import {변수1,변수2} from '.assets/room.js'
// export에서 { } 썼으면 import에서도 써줘야함
export default {
  name: 'App',
  //데이터 보관
  data(){
    return {
      price1 : 60,
      price2 : 70,
      스타일 : 'color : blue',
      products : products,
      menus : ['home', 'shop', 'about'],
      신고수 : [0,0,0],
      modal : false,
    }
  },
  
  // function 만드는 곳
  // 데이터를 쓰려면 this를 써줘야함
  methods : {
    plus(i){
      this.신고수[i]++
    },
  },

  components: {
  }
}
</script>

